.login{
    height: 100vh;
    background: #f8f8f8;
    display: grid;
    place-items: center;
}
.login_container{
    padding:100px;
    text-align: center;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 1px 3px;
}

.login_container>img{
object-fit:contain;
height: 100px;
margin-bottom: 40px;
}

.login_container>button{
    margin-top: 50px;
    text-transform: inherit; 
    background-color:#0a8d48; 
    color:white; 

}