.chat{
    flex: 0.7;
    flex-grow: 1;
    overflow-y: scroll;
    padding-bottom: 150px;
}

.chat_header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    border-bottom: 1px solid lightgray;

}

.chat_channelName{
    display: flex;
    text-transform: lowercase;
}

.chat_headerRight>p{
display: flex;
align-items: center;
font-size: 14px;
}

.chat_headerRight>p>.MuiSvgIcon-root{
margin-right: 5px !important;
font-size: 16px;

}
.chat_channelName>.MuiSvgIcon-root{
    margin-left: 10px;
    font-size: 18px;
}